import React,  { useState, useContext } from 'react'
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { ContentContext } from '../../App';
import './style.css';
var { DateTime } = require('luxon');

export default function HabitElement(props) {
    const [todayCompleted, setTodayCompleted] = useState(false)
    const {setNewContext, setDB} = useContext(ContentContext);

    if (props.today == DateTime.now().c.day && todayCompleted === false) {
        setTodayCompleted(true)
    }

    let saveHabit = async function () {
        console.log(props.object)
        let saveVar = {habitTracking: {longestStreak: props.object.habitTracking.longestStreak, currentStreak: props.object.habitTracking.currentStreak, lastDone: DateTime.now().c.day}, key: props.object.key, habitName: props.object.habitName, habitConditions: props.object.habitConditions, habitEffects: props.object.habitEffects, habitAuth: props.object.habitAuth, habitNo: props.object.habitNo}
        console.log(saveVar)    
        await setDB(saveVar, saveVar);
        setTodayCompleted(true)
    }

    return (
        <div>
            <h3>{props.name}</h3>
            <p>Current Streak: {props.cStreak}</p>
            <p>Longest Streak: {props.lStreak}</p>
            {todayCompleted != true  ?
            <button onClick={()=>{saveHabit()}}>Mark as done</button>
        :
        <button>Completed</button>
        }
            <button onClick={()=>{setNewContext(1, props.object)}}>Edit</button>
        </div>
    )
}
